import React, { Component } from 'react'
import { Alert, Modal } from 'react-bootstrap';
import { CLOUDPATH, HOST_URL, HOST_URL_TAGGSHOP } from '../../../../actions/api'
import { TOAST_ERROR, TOAST_SUCCESS, showAddAccounntAlert } from '../../../../utils';


export default class AddAccountPopup extends Component {

    onAddAccount = (item) => {
        showAddAccounntAlert(item, false, false)
        this.props.openAddAccountPopup()
    }
    render() {
        const { addAccountPopup, openAddAccountPopup, addAccountData , authorized} = this.props;
        return (
            <>
                <Modal size="md" dialogClassName="modal-dialog-centered" show={addAccountPopup} onHide={openAddAccountPopup} >
                    <Modal.Header>
                        <Modal.Title>Pick a source</Modal.Title>
                        <button type="button" className="close" data-bs-dismiss="modal" onClick={openAddAccountPopup}>
                            <i aria-hidden="true" className="ki ki-close"></i>
                        </button>
                    </Modal.Header>
                    <Modal.Body className="body-bg">
                            <ul className="networks modal_networks">
                                {addAccountData ? addAccountData.map(item => (
                                    <li className="twitter" onClick={() => this.onAddAccount(item)}>

                                 {/* {(item.id == 4) ?
                                    <a href="javascript:(0)" className={`tooltips popUp noLoader`}  onClick={openAddAccountPopup}>
                                             <img className="img-fluid" src={item.iconImg} />
                                            <span>{item.name}</span></a>
                                    : (item.id == 20) ? <a href="javascript:(0)" className={`tooltips popUp noLoader  picksource `}  onClick={openAddAccountPopup}>
                                             <img className="img-fluid" src={item.iconImg} />
                                            <span>{item.name}</span></a>
                                        : (item.id == 24) ? <a href="javascript:(0)" className={`tooltips popUp noLoader picksource  `}   onClick={openAddAccountPopup}>
                                                 <img className="img-fluid" src={item.iconImg} />
                                            <span>{item.name}</span></a>
                                            : (item.id == 10) ? <a href="javascript:(0)" className={`tooltips popUp noLoader  picksource `}   onClick={openAddAccountPopup}>
                                                     <img className="img-fluid" src={item.iconImg} />
                                            <span>{item.name}</span></a>
                                                : <a href="javascript:(0)" className={`tooltips popUp noLoader picksource  `} onClick={openAddAccountPopup}>
                                                         <img className="img-fluid" src={item.iconImg} />
                                            <span>{item.name}</span></a>
                                  } */}
                                        <a className={`tooltips popUp noLoader`} >
                                            <img className="img-fluid" src={`${CLOUDPATH}taggshop/app/assets/media/square-icons/${item.icon}.svg?ver=1`} />
                                            <span>{item.name}</span>
                                        </a>
                                    </li>
                                )) : ''}
                            </ul>
                    </Modal.Body>
                </Modal>
            </>
        )
    }
}
