import React, { Component } from "react";
import { Modal, OverlayTrigger, Tooltip } from "react-bootstrap";
import { connect } from "react-redux";
import AddAccountPopup from "../../accounts/elements/AddAccountPopup";
import { DELETE_USER_SOCIAL_ACCOUNT, TAGGBOX_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA } from "../../../../actions/sceneActions";
import ReactTooltip from "react-tooltip";
import { CLOUDPATH_TAGGSHOP } from "../../../../actions/api";
import SocialAccountsCard from "../../accounts/elements/SocialAccountsCard";
import Swal from "sweetalert2";

class SocialAccount extends Component {
  state = { allSocialAcc: this.props.socialAccounts, addAccountPopup: false, shareAccount: false, errorShareAccount: null }
 
  deleteSocialAccount = (item) => event => {
    Swal.fire({
        title: "Are you sure?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes",
        cancelButtonText: "No",
    }).then((result) => {
        if (result.value) {
            // this.props.DELETE_USER_SOCIAL_ACCOUNT(item.ConnectedAccounts.id, item.Network.id, this.onSaveCallBack);
        }
        else if (result.dismiss === "cancel") { }
    });
}
openAddAccountPopup = () => {
  const { addAccountPopup } = this.state;
  this.setState({ addAccountPopup: !addAccountPopup })
  this.props.TAGGBOX_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA();
}

  render() {
    const { addAccountData } = this.props
    const { allSocialAcc, addAccountPopup, shareAccount, errorShareAccount } = this.state;
    const sicialData = allSocialAcc && allSocialAcc.accountInformations && allSocialAcc.accountInformations.length > 0 ? true : false;

    return (
      // isLoading ?
        <>
          <div className="d-flex flex-column-fluid main_content account_info_wrap subheader_content">
            <div className="container">
              <div className="row">
                <div className="col-md-12">
                  <div className="screen_wrapper pb-10 pt-5">
                    <div className="tab-content">
                      <div id="connectedAccounts" className="tab-pane fade active show">
                        <h4 className="text-dark font-weight-bold my-1 mb-4">Social Accounts</h4>
                        <div className="card card-custom">
                          <div className="card-header flex-wrap py-6 justify-content-end">
                            <div className="card-toolbar">
                              <button className="btn btn-primary" data-bs-toggle="modal" id="addAccount" data-target="#addAccount" onClick={this.openAddAccountPopup}>
                                <span className="svg-icon svg-icon-md">
                                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                    <g id="Group_4424" data-name="Group 4424" transform="translate(10922 -4685)">
                                      <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10922 4685)" fill="none" opacity="0"></rect>
                                      <path id="Path_5219" data-name="Path 5219" d="M-15.382,10.935h-6.172V4.765A1.065,1.065,0,0,0-22.618,3.7a1.065,1.065,0,0,0-1.064,1.064v6.17h-6.174a1.058,1.058,0,0,0-.751.317,1.056,1.056,0,0,0-.305.756,1.068,1.068,0,0,0,1.058,1.055h6.172v6.169a1.059,1.059,0,0,0,.311.754,1.021,1.021,0,0,0,.755.313,1.055,1.055,0,0,0,.749-.309,1.059,1.059,0,0,0,.313-.756V13.063h6.175a1.066,1.066,0,0,0,1.057-1.072A1.069,1.069,0,0,0-15.382,10.935Z" transform="translate(-10887.383 4685)" fill="#545454"></path>
                                    </g>
                                  </svg>
                                </span>
                                Add Account
                              </button>
                            </div>
                          </div>



                          <div className="card-body p-5">
                            {sicialData ?
                              <div className="table-responsive">
                                <div className="accordion accordion-light accordion-svg-toggle conn_accounts_wrap" id="connectedAccounts">
                                  <div className="connectedAccounts_thead">
                                    <ul className="list-inline row m-0">
                                      <li className="col-3">
                                        <p className="font-weight-bolder text-dark">Network</p>
                                      </li>
                                      <li className="col-2">
                                        <p className="font-weight-bolder text-dark">Account</p>
                                      </li>
                                      <li className="col-2">
                                        <p className="font-weight-bolder text-dark">Token Validity</p>
                                      </li>
                                      <li className="col-2">
                                        <p className="font-weight-bolder text-dark">Status</p>
                                      </li>
                                      <li className="col-2">
                                        <p className="font-weight-bolder text-dark">Refresh</p>
                                      </li>
                                    </ul>
                                  </div>
                                  <div className="connectedAccounts">
                                    {allSocialAcc.accountInformations.map((item, index) => (
                                      <SocialAccountsCard item={item} index={index} key={index} deleteSocialAccount={this.deleteSocialAccount} fromShareAccount={true} />
                                    ))}
                                  </div>
                                </div>
                              </div> 
                              :
                              <div className="no_connected_acc">
                                <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/products/product-empty.svg`} className="mb-10 img-fluid" />
                                <h4 className="h1 mb-4">Account Not Connected</h4>
                              </div>
                            }
                          </div>

                          <div className="mt-10 pt-10 pb-5 border-top">
                            <div className="col-sm-12 px-3">
                              <p className="font-size-sm bg-light p-3">NOTE: Tagshop does not have the option to access, store, or view personal information and passwords that you may use to connect your social media accounts.</p>
                            </div>
                          </div>


                        </div>

                        <AddAccountPopup openAddAccountPopup={this.openAddAccountPopup} addAccountPopup={addAccountPopup} addAccountData={addAccountData} />

                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
        // : <LoadingScreen />
    );
  }
}
const mapStateToProps = (state) => {
  return {
    addAccountData: state.userAccountData.addAccount,
    socialAccounts: state.userAccountData.socialAccounts,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    DELETE_USER_SOCIAL_ACCOUNT: (feedId, networkId, callBack) =>
      dispatch(DELETE_USER_SOCIAL_ACCOUNT(feedId, networkId, callBack)),
      TAGGBOX_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA: () =>
      dispatch(TAGGBOX_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SocialAccount);
