import React, { useState } from 'react'
import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { GET_CONNECTION_DETAIL } from '../../../actions/shareAccountAction';
import LoadingScreen from '../../pages/LoadingScreen';
import SocialAccount from './components/SocialAccount';
import { useDispatch } from 'react-redux';
import { TAGGBOX_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA, TAGGSHOP_SOCIAL_ACCOUNT_DATA } from '../../../actions/sceneActions';
import { addAccountFromId } from '../../../utils/addAccount';
import { GET_LOCAL } from '../../../utils';

const controller = new AbortController();
const ShareAccount = () => {
    const params = useParams();
    const dispatch = useDispatch();
    const [loading,setLoading] = useState(true);

    const handleMessage = async(event) => {
        console.log(event)
        const networkId = GET_LOCAL('networkId')
        console.log('networkId', networkId)
        const { error, code } = event.data;
        if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
            if (code) {
                await addAccountFromId(networkId, code, dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA(atob(params.id).split("'").join(''), setLoading)))
            }
        }
    };

    useEffect(() => {
        const signal = controller.signal;
        window.addEventListener("message", handleMessage, { signal });
        dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA(atob(params.id).split("'").join(''), setLoading))
        localStorage.setItem('ownerId', atob(params.id).split("'").join(''))
        localStorage.setItem('shareAccount', true)
        localStorage.setItem('url_redirect', window.location.pathname.replace('/v2', ''))
        return () => {
            controller.abort()
        }
    }, [])
    
    return loading? <LoadingScreen/>:<SocialAccount/>
}

export default ShareAccount