import moment from 'moment';
import React, { Component } from 'react'
import { CLOUDPATH, HOST_URL_TAGGSHOP } from '../../../../actions/api'
import ReactTooltip from "react-tooltip";
import RefreshSvg from './RefreshSvg'
import { AUTHOR_IMAGE, showAddAccounntAlert } from '../../../../utils'

export default class SocialAccountsCard extends Component {

    render() {
        const { item, index, deleteSocialAccount, authorized, fromShareAccount } = this.props;
        const networkIcon = item.Network.description.toLowerCase().replace(" ", "-");
        const profilePic = item.ConnectedAccounts.profilePicture;
        const validity = moment.unix(item.ConnectedAccounts.validity).format('DD MMM YYYY');
        const isChild = item.ConnectedAccounts.child;
        return (
            <div className="card">
                <div className="conn_table connectedAccounts_list">
                    <div className="row align-items-center m-0">
                        <figure className="col-3 card-header ">
                            <div className="d-flex align-items-center flex-nowrap card-title collapsed" >
                                <div className="flex-shrink-0 mr-4 mt-lg-0 mt-3 d-flex align-items-center">
                                    <div className="symbol symbol-35">
                                        <img className="img-fluid" src={`${CLOUDPATH}taggshop/app/assets/media/square-icons/${networkIcon}.svg?ver=1`} alt="" />
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className="font-weight-normal text-dark mb-0 text-nowrap">{item.Network.name}</p>
                                </div>
                                {isChild && isChild.length > 0 ? <span className="svg-icon ms-3" data-bs-toggle="collapse" data-bs-target={`#Network_id${item.ConnectedAccounts.id}`} aria-expanded="false">
                                    <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                        <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                            <polygon points="0 0 24 0 24 24 0 24"></polygon>
                                            <path d="M6.70710678,15.7071068 C6.31658249,16.0976311 5.68341751,16.0976311 5.29289322,15.7071068 C4.90236893,15.3165825 4.90236893,14.6834175 5.29289322,14.2928932 L11.2928932,8.29289322 C11.6714722,7.91431428 12.2810586,7.90106866 12.6757246,8.26284586 L18.6757246,13.7628459 C19.0828436,14.1360383 19.1103465,14.7686056 18.7371541,15.1757246 C18.3639617,15.5828436 17.7313944,15.6103465 17.3242754,15.2371541 L12.0300757,10.3841378 L6.70710678,15.7071068 Z" fill="#545454" fillRule="nonzero" transform="translate(12.000003, 11.999999) rotate(-180.000000) translate(-12.000003, -11.999999) "></path>
                                        </g>
                                    </svg>
                                </span> : ''}
                            </div>
                        </figure>
                        <figure className="col-2">
                            <div className="d-flex align-items-center flex-nowrap">
                                <div className="flex-shrink-0 mr-4 mt-lg-0 mt-3">
                                    <div className="symbol symbol-circle connected_user_p" 
                                    style={{ backgroundImage: `url(${profilePic.replace(/\s/g, '')}),url(${AUTHOR_IMAGE(item.ConnectedAccounts.accountName, item.Network.color)})` }}
                                    ></div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className="text-dark mb-0">{item.ConnectedAccounts.accountName}</p>
                                </div>
                            </div>
                        </figure>
                        <figure className="col-2">{validity}</figure>
                        <figure className="col-2">
                            <div className="d-flex align-items-center">
                                {item.ConnectedAccounts.api == 2 ?
                                    <span className="label font-weight-bold label-xl label-inline label-light-success acc_status">Connected</span>
                                    : <span className="label font-weight-bold label-xl label-inline label-light-danger acc_status">Disconnected</span>
                                }
                                <ReactTooltip effect='solid' />
                                {item.ConnectedAccounts.api !== 2 && item.ConnectedAccounts.cronStatus ?
                                    <div className="d-flex mx-2 align-items-center" data-tip={item.ConnectedAccounts.cronStatus} data-theme="dark" title="" >
                                        <span className="svg-icon svg-icon-secondary svg-icon-md">
                                            <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                    <rect x="0" y="0" width="24" height="24"></rect>
                                                    <circle fill="#545454" opacity="0.3" cx="12" cy="12" r="10"></circle>
                                                    <rect fill="#ffffff" x="11" y="10" width="2" height="7" rx="1"></rect>
                                                    <rect fill="#ffffff" x="11" y="7" width="2" height="2" rx="1"></rect>
                                                </g>
                                            </svg>
                                        </span>
                                    </div> : ''}
                            </div>
                        </figure>
                        <figure className="col-2">
                            {item.ConnectedAccounts.api == 2 ? <>

                                    <a className="btn btn-primary btn-sm" onClick={() => showAddAccounntAlert(item.Network, true)} ><RefreshSvg /> Refresh</a>
                                 </> :

                                <>
                                    <a className="btn btn-primary btn-sm" onClick={() => showAddAccounntAlert(item.Network, true)} ><RefreshSvg /> Reconnect</a>
                                </>}


                                 

                        </figure>
                        {!fromShareAccount ? <figure className="col-1 text-right">
                            <div className="btn btn-link btn-icon btn-clean" onClick={deleteSocialAccount(item)} >
                                <span className="svg-icon-lg svg-icon-dark-75">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                        <g data-name="Group 4331" transform="translate(10956 -4533)">
                                            <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                            <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                                            </g>
                                        </g>
                                    </svg>
                                </span>
                            </div>
                        </figure> : <></>}
                    </div>
                </div>
                <div id={`Network_id${item.ConnectedAccounts.id}`} className="accordion-collapse collapse" data-bs-parent="#connectedAccounts">
                    <div className="card-body conn_acc_more">
                        <div className="table-responsive">
                            <table className="table table-vertical-center" id="example70046">
                                <thead className="bg-gray-100">
                                    <tr>
                                        <th scope="col">#</th>
                                        <th scope="col"><span>Page profile</span></th>
                                        <th scope="col"><span>User Name</span></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {isChild && isChild.length > 0 ? isChild.map((childData, childIndex) => (
                                        <tr key={childIndex}>
                                            <td>{childIndex + 1}</td>
                                            <td>
                                                <div className="symbol symbol-circle connected_user_p"
                                                style={{ backgroundImage: `url(${childData.ConnectedAccounts.profilePicture.replace(/\s/g, '')}),url(${AUTHOR_IMAGE(childData.ConnectedAccounts.value2, childData.Network.color)})` }}
                                                ></div>
                                            </td>
                                            <td>
                                                <span className="font-weight-bold">{childData.ConnectedAccounts.value2}</span>
                                            </td>
                                        </tr>

                                    )) : ''}

                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


